<template>
  <div class="addressPad">
    <!-- 地址模板 -->
    <!-- 顶部图片 -->
    <div class="addressPsd_img" v-if="index == 0">
      <img src="http://47.115.51.185/h5/img/line.05bf1c84.jpg" alt="" />
    </div>
    <div class="addressPsd_item">
      <div class="addressPsd_t">
        <h3>
          收货人：{{ item.real_name }}<span>{{ item.phone }}</span>
        </h3>
        <p>
          收货地址：{{ item.province }}{{ item.city }}{{ item.district
          }}{{ item.detail }}
        </p>
      </div>
      <div class="addressPsd_b">
        <!-- 设置默认 -->
        <div class="none">
          <!-- <van-radio-group v-model="radio1" @change="ch">
            <van-radio :name="index">设为默认</van-radio>
          </van-radio-group> -->
          <label @click="set"
            ><input
              type="radio"
              name="address"
              ref="radio"
              :checked="item.is_default"
            /><i :class="item.is_default?'active':''"></i><span>设为默认</span></label
          >
        </div>
        <!--               :checked="index==0?true:false" -->
        <div class="none2">
          <!-- 编辑 -->
          <div class="editor">
            <i class="iconfont icon-iconfontzhizuobiaozhunbduan10"></i
            ><span>编辑</span>
          </div>
          <!-- 删除 -->
          <div class="del" @click="del">
            <i class="iconfont icon-shanchu"></i><span>删除</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["item", "index"],
  created() {
    // console.log(this.item);
    // console.log(this.index);
    this.check = this.item.is_default;
  },
  data() {
    return {
      // 是否选中
      check: null,
      radio1: "",
    };
  },
  mounted() {
    //   console.log(this.$refs.radio);
    this.check = this.$refs.radio;
    // this.$refs.radio.parentNode.children[1].classList.add("active");
  },
  methods: {
    change() {},
    ch(val) {
      console.log(val);
    },
    // 设置默认地址
    set(){
      this.axios.post("/api/address/default/set",{
        id: this.item.id
      }).then(()=>{
        this.$emit("update");
      })
    },
    // 删除地址
    del(){
      this.axios.post("/api/address/del",{
        id: this.item.id
      }).then(()=>{
        this.$emit("update");
      })
    }
  },
  watch: {
    flag(news) {
      console.log(news);
    },
  },
};
</script>
<style lang="less" scoped>
.addressPad {
  background: #fff;
  margin-bottom: 0.3rem;
  .addressPsd_img {
    width: 100%;
    height: 0.03rem;
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  .addressPsd_item {
    width: 100vw;
    box-sizing: border-box;
    padding: 0 0.34rem;
    // 上
    .addressPsd_t {
      padding: 0.3rem 0;
      border-bottom: 1px solid #eee;
      font-size: 0.32rem;
      color: #282828;
      text-align: left;
      h3 {
        font-size: 0.32rem;
        font-weight: 700;
        margin-bottom: 0.08rem;
      }
    }
    // 下
    .addressPsd_b {
      height: 0.83rem;
      font-size: 0.28rem;
      color: #282828;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0.2rem 0;
      //   单选框
      .none {
        height: 0.68rem;
        position: relative;
        label {
          display: flex;
          align-items: center;
          input {
            height: 0.582rem;
            width: 0.582rem;
            position: relative;
            z-index: 19;
            color: #e93323 !important;
            background: #e93323 !important;
            opacity: 0;
          }
          i {
            display: inline-block;
            height: 0.582rem;
            width: 0.582rem;
            border-radius: 50%;
            position: absolute;
            top: 0;
            left: 0;
            border: 0.001rem solid #838383;
            background-image: url("data:image/png;base64,");
            background-position: 50%;
            background-size: 0.31rem 0.25rem;
            background-repeat: no-repeat;
          }
          i.active {
            background-color: #e93323 !important;
          }
          span {
            margin-left: 0.2rem;
          }
        }
      }
      // 其他
      .none2 {
        display: flex;
        justify-content: space-around;
        align-items: center;
        font-size: 0.38rem;
        i {
          color: #2c2c2c;
          font-size: 0.3rem;
          vertical-align: -0.02rem;
          margin-right: 0.14rem;
        }
        .del {
          margin-left: 0.58rem;
        }
      }
    }
  }
}
</style>