<template>
  <div id="address">
    <!-- 地址 -->
    <ul class="address_list">
      <li v-for="(i, index) in addressList" :key="i.id">
        <!-- <AddressPad :item="i" :index="index + 1"></AddressPad> -->
        <AddressPad :item="i" :index="index" @update="update"></AddressPad>
      </li>
    </ul>
    <!-- 添加新地址 -->
    <router-link to="/add_address" tag="div" class="address_btn">
      <van-button icon="location-o" color="#e93323" type="primary"
        >添加新地址</van-button
      >
    </router-link>
  </div>
</template>
<script>
import AddressPad from "../components/address/AddressPsd";
export default {
  components: {
    AddressPad,
  },
  data() {
    return {
      addressList: [],
    };
  },
  created() {
    this.getList();
  },
  methods: {
    // 获取地址列表
    getList() {
      this.axios.get("/api/address/list?page=1&limit=20").then((res) => {
        this.addressList = res.data.data;
        // console.log(this.addressList);
      });
    },
    // 默认地址改变就更新
    update(){
      this.getList();
    }
  },
};
</script>
<style lang="less" scoped>
#address {
  width: 100vw;
  // height: 100vh;
  // background-color: #f5f5f5;
  // 添加新地址
  .address_btn {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    background-color: #fff;
    padding: 0.2rem 0;
    .van-button {
      width: 8.8rem;
      margin: 0 auto;
      height: 0.98rem;
      border-radius: 0.5rem;
      text-align: center;
      line-height: 0.98rem;
      font-size: 0.36rem;
      color: #fff;
    }
  }
}
</style>